"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import Navbar from "@/components/common/Navbar";

export default function AvailableTimesPage() {
  const [selectedDays, setSelectedDays] = useState<string[]>([]);
  const [acceptUrgent, setAcceptUrgent] = useState(true);
  const [timeZone, setTimeZone] = useState("Eastern Time (ET)");
  const [responseTime, setResponseTime] = useState("Within 3hrs");

  const weekdays = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"];
  const weekends = ["Saturday", "Sunday"];
  const everyday = [...weekdays, ...weekends];

  const toggleDay = (day: string) => {
    setSelectedDays((prev) =>
      prev.includes(day)
        ? prev.filter((d) => d !== day)
        : [...prev, day]
    );
  };

  const applyPreset = (days: string[]) => setSelectedDays(days);

  return (
    <div className="min-h-screen bg-white flex flex-col">
      <Navbar />

      {/* CENTER CONTAINER */}
      <div className="w-full flex justify-center mt-10 px-4">
        <div className="w-full max-w-[430px] flex flex-col">

          {/* Title */}
          <p className="text-[18px] font-semibold text-gray-800 text-left">
            Step 4 : Set your Available Times
          </p>

          {/* Progress Bar */}
          <div className="w-full h-[6px] bg-gray-200 rounded-full mt-3 mb-7">
            <div className="h-full bg-[#2D74FF] rounded-full" style={{ width: "66%" }} />
          </div>

          {/* WHITE CARD */}
          <motion.div
            initial={{ opacity: 0, y: 15 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.25 }}
            className="w-full bg-white rounded-2xl shadow-[0_4px_18px_rgba(0,0,0,0.1)] border border-gray-100 p-6"
          >
            {/* Time Zone + Response Time */}
            <div className="flex gap-4 mb-6">
              <div className="flex-1">
                <label className="text-sm text-gray-700 font-medium">Time Zone*</label>
                <select
                  value={timeZone}
                  onChange={(e) => setTimeZone(e.target.value)}
                  className="mt-1 w-full border border-gray-300 rounded-xl px-3 py-2.5 text-sm"
                >
                  <option>Eastern Time (ET)</option>
                  <option>Central Time (CT)</option>
                  <option>Mountain Time (MT)</option>
                  <option>Pacific Time (PT)</option>
                </select>
              </div>

              <div className="flex-1">
                <label className="text-sm text-gray-700 font-medium">Responds Time*</label>
                <select
                  value={responseTime}
                  onChange={(e) => setResponseTime(e.target.value)}
                  className="mt-1 w-full border border-gray-300 rounded-xl px-3 py-2.5 text-sm"
                >
                  <option>Within 1hr</option>
                  <option>Within 3hrs</option>
                  <option>Within 6hrs</option>
                  <option>Within 12hrs</option>
                </select>
              </div>
            </div>

            {/* Quick Presets */}
            <div className="flex gap-2 flex-wrap items-center mb-6">
              <button
                onClick={() => applyPreset(weekdays)}
                className="px-4 py-1.5 rounded-full border border-gray-300 text-[13px]"
              >
                Weekdays
              </button>

              <button
                onClick={() => applyPreset(weekends)}
                className="px-4 py-1.5 rounded-full border border-gray-300 text-[13px]"
              >
                Weekends
              </button>

              <button
                onClick={() => applyPreset(everyday)}
                className="px-4 py-1.5 rounded-full border border-gray-300 text-[13px]"
              >
                Everyday
              </button>

              <span
                className="text-[#2D74FF] ml-auto text-sm cursor-pointer"
                onClick={() => setSelectedDays([])}
              >
                Clear
              </span>
            </div>

            {/* Days Checkboxes */}
            <div className="flex flex-col gap-3 mb-6">
              {everyday.map((day) => (
                <label
                  key={day}
                  onClick={() => toggleDay(day)}
                  className={`flex items-center justify-between border rounded-xl px-4 py-2.5 cursor-pointer transition ${
                    selectedDays.includes(day)
                      ? "border-blue-500 bg-blue-50"
                      : "border-gray-300"
                  }`}
                >
                  <span className="text-sm font-medium text-gray-800">{day}</span>
                  <input
                    type="checkbox"
                    checked={selectedDays.includes(day)}
                    readOnly
                    className="h-4 w-4 accent-blue-600"
                  />
                </label>
              ))}
            </div>

            {/* Urgent Requests */}
            <div className="bg-[#FFF6F0] rounded-xl border border-gray-200 px-4 py-4 flex justify-between items-center">
              <div>
                <p className="text-sm font-semibold text-gray-800">Accept Urgent Requests</p>
                <p className="text-xs text-gray-500">
                  Get notified for urgent jobs even outside your working hours
                </p>
              </div>

              {/* Toggle */}
              <div
                onClick={() => setAcceptUrgent(!acceptUrgent)}
                className={`w-12 h-6 rounded-full flex items-center cursor-pointer transition ${
                  acceptUrgent ? "bg-green-500" : "bg-gray-300"
                }`}
              >
                <div
                  className={`bg-white w-5 h-5 rounded-full shadow transform transition ${
                    acceptUrgent ? "translate-x-6" : "translate-x-1"
                  }`}
                />
              </div>
            </div>
          </motion.div>

          {/* Buttons */}
          <div className="flex w-full justify-between mt-6 mb-10">
            <button className="w-[48%] py-2.5 rounded-xl border border-gray-300 text-gray-700 text-[15px]">
              Back
            </button>

            <button className="w-[48%] py-2.5 rounded-xl bg-[#2D74FF] text-white text-[15px]">
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
